import React, { useState, useRef, useEffect, useMemo, useCallback } from "react";
import ReactQuill, { Quill } from "react-quill-new";
import 'react-quill-new/dist/quill.snow.css'; // quill  
import { postBoardRequest, uploadImage, uploadFileRequest } from "../apis";
import { useNavigate } from "react-router-dom";
import { useCookies } from "react-cookie";
import { useBoardStore, useLoginuserStore } from "../stores/store";

export default function BoardWrite() {
  const quillRef = useRef();
  const navigate = useNavigate();
  const [cookies] = useCookies(['accessToken']);
  const { title, setTitle, content, boardImageList, setBoardImageList, category, setCategory, setContent, resetBoard } = useBoardStore();
  const { loginUser, setLoginUser, resetLoginUser } = useLoginuserStore();
  const [uploadedUrls, setUploadedUrls] = useState([]); //   URL 
  const [memberName, setMemberName] = useState('');
  const [memberEmail, setMemberEmail] = useState('');
  const [memberDegree, setMemberDegree] = useState('');
  const [memberImage, setMemberImage] = useState('');

  const imageHandler = useCallback(() => {
    const input = document.createElement('input');
    input.setAttribute('type', 'file');
    input.setAttribute('accept', 'image/*');
    
    input.onchange = async () => {
      const file = input.files[0];
      if (file) {
        try {
          const accessToken = cookies.accessToken;
          const imageUrl = await uploadImage(file, accessToken);
          
          if (imageUrl) {
            const quillEditor = quillRef.current.getEditor();
            const range = quillEditor.getSelection(true);
            
            // URL  
            const cleanUrl = imageUrl.replace('http://localhost:8080', '');
            const fullUrl = `http://localhost:8080${cleanUrl}`;
            
            console.log('  URL:', fullUrl); // URL 
            
            quillEditor.insertEmbed(range.index, 'image', fullUrl);
            
            //     
            const image = quillEditor.root.querySelector(`img[src="${fullUrl}"]`);
            if (image) {
              image.style.maxWidth = '100%';
              image.style.height = 'auto';
            }
            
            setUploadedUrls(prev => [...prev, fullUrl]);
          }
        } catch (error) {
          console.error('  :', error);
          alert('  .');
        }
      }
    };

    input.click();
  }, [cookies.accessToken]);

  //   modules 
  const modules = useMemo(() => ({
    toolbar: {
      container: [
        [{ header: [1, 2, 3, false] }],
        ["bold", "italic", "underline", "strike"],
        ["image"],
        [{ list: "ordered" }, { list: "bullet" }],
        [{ align: [] }],
        ["clean"],
      ],
      handlers: {
        image: imageHandler,
      },
    },
  }), [imageHandler]);

  //  API  
  const postBoardResponse = (responseBody) => {
    if (!responseBody) return;
    const { code } = responseBody;
    if (code === 'AF' || code === 'NU') {
      navigate('/');
    }
    if (code === 'VF') alert('  .');
    if (code === 'DE') alert(' .');
    if (code !== 'SU') return;
    
    alert(' !');
    resetBoard();
    setTitle('');
    setCategory('');
    setContent('');

    const quillEditor = quillRef.current.getEditor();
    quillEditor.setText(''); //   
    if (!loginUser) return;
  };

  //     
  const onSubmitButtonClickHandler = async () => {
    try {
      const accessToken = cookies.accessToken;
      let finalContent = content;

      if (category === 'Members') {
        //   
        if (!memberName || !memberEmail || !memberDegree || !uploadedUrls[0]) {
          alert('  .');
          return;
        }

        finalContent = JSON.stringify({
          name: memberName,
          email: memberEmail,
          degree: memberDegree,
          image: uploadedUrls[0]
        });
      }

      const requestBody = {
        title,
        content: finalContent,
        category,
        boardImageList: uploadedUrls
      };
      
      const response = await postBoardRequest(requestBody, accessToken);
      
      if (response.code === 'SU') {  //   
        alert(' .');
        if (category === 'Members') {
          navigate('/members');  // Members   members  
        } else {
          navigate('/');
        }
        resetBoard();
      } else {
        alert('  .');
      }

    } catch (error) {
      console.error('    :', error);
      alert('  .');
    }
  };

  //     
  useEffect(() => {
    const accessToken = cookies.accessToken;
    if (!accessToken) {
      navigate('/');
      return;
    }
    resetBoard();
  }, [cookies, navigate, resetBoard]);

  useEffect(() => {
    if (category === 'Members') {
      setTitle('Members'); // Members     
    }
  }, [category]);

  useEffect(()=>{
    console.log(content)
  },[ content])

  return (
    <div>
      <div style={{ width: "100%", height: "90vh" }}>
        <div style={{ width: "1000px", margin: "auto", borderRadius: "19px" }}>
          <div
            style={{
              marginBottom: "20px",
              marginTop: "40px",
              fontSize: "20px",
              fontWeight: "bold",
            }}
          >
            
          </div>

          {/* ======== Subject ======== */}
          <div className="flex gap-4">
            <input
              className="Subject"
              placeholder="  "
              style={{
                padding: "7px",
                marginBottom: "10px",
                width: "100%",
                border: "1px solid lightGray",
                fontSize: "15px",
              }}
              value={title}
              onChange={(e) => setTitle(e.target.value)}
            />
            <select
              className="Category"
              style={{
                padding: "7px",
                marginBottom: "10px",
                width: "20%",
                border: "1px solid lightGray",
                fontSize: "15px",
              }}
              value={category}
              onChange={(e) => setCategory(e.target.value)}
            >
              <option value={null}> </option>
              <option value="Professor">Professor</option>
              <option value="Notice">Notice</option>
              <option value="Research">Research</option>
              <option value="Members">Members</option>
              <option value="Project">Project</option>
              <option value="CoverSelection">CoverSelection</option>
            </select>
          </div>

          {category === 'Members' ? (
            <div className="member-inputs space-y-4">
              <div>
                <input
                  type="file"
                  accept="image/*"
                  onChange={async (e) => {
                    const file = e.target.files[0];
                    if (file) {
                      try {
                        const imageUrl = await uploadImage(file, cookies.accessToken);
                        setMemberImage(imageUrl);
                        setUploadedUrls([imageUrl]);
                      } catch (error) {
                        console.error('  :', error);
                      }
                    }
                  }}
                />
              </div>
              <input
                className="w-full p-2 border rounded"
                placeholder=""
                value={memberName}
                onChange={(e) => setMemberName(e.target.value)}
              />
              <input
                className="w-full p-2 border rounded"
                placeholder=""
                value={memberEmail}
                onChange={(e) => setMemberEmail(e.target.value)}
              />
              <select
                className="w-full p-2 border rounded"
                value={memberDegree}
                onChange={(e) => setMemberDegree(e.target.value)}
              >
                <option value=""> </option>
                <option value="Part Time Ph.D. Students">Part Time Ph.D. Students</option>
                <option value="Master Students">Master Students</option>
                <option value="Ph.D. Students">Ph.D. Students</option>
              </select>
            </div>
          ) : (
            <div style={{ height: "650px" }}>
              <ReactQuill
                ref={quillRef}
                modules={modules}
                placeholder="  "
                onChange={(value) => setContent(value)}
                style={{ height: "600px" }}
              />
            </div>
          )}

          {/* ======== Button ======== */}
          <div style={{ float: "right" }}>
            <button style={{ marginRight: "10px" }}></button>
            <button onClick={onSubmitButtonClickHandler}></button>
          </div>
        </div>
      </div>
    </div>
  );
}
