import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import PageTitle from "../components/PageTitle";
import { getBoardDetail, deleteBoardRequest } from "../apis";
import { useCookies } from 'react-cookie';
import { useLoginuserStore } from "../stores/store";

export default function BoardDetail() {
  const [data, setData] = useState(null); //    
  const location = useLocation(); //  URL  
  const searchParams = new URLSearchParams(location.search); //   
  const boardNumber = searchParams.get("board_number"); // 'board_number'  
  const [cookies] = useCookies(['accessToken']);
  const navigate = useNavigate();
  const { loginUser } = useLoginuserStore();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await getBoardDetail(boardNumber);
        if (response) {
          setData(response);
          console.log('  :', response);
        }
      } catch (error) {
        console.error("    :", error);
      }
    };
    
    if (boardNumber) {
      fetchData();
    }
  }, [boardNumber]); // boardNumber   

  const handleEdit = () => {
    navigate(`/board/edit/${boardNumber}`);
  };

  const handleDelete = async () => {
    if (!cookies.accessToken) {
        alert(' .');
        return;
    }

    if (!window.confirm('   ?')) return;

    try {
        const response = await deleteBoardRequest(boardNumber, cookies.accessToken);
        console.log('Delete response:', response);
        
        if (response.code === 'SU') {
            alert(' .');
            //        
            const prevPath = document.referrer;
            if (prevPath && prevPath.includes(window.location.origin)) {
                window.history.back();
                console.log('  ');
            } else {
                navigate('/');
            }
        } else {
            alert('  .');
        }
    } catch (error) {
        console.error('   :', error);
        if (error.response?.status === 403) {
            alert('  .');
        } else {
            alert('  .');
        }
    }
  };

  return (
    <div>
      <PageTitle />
      <div className="mx-16 mt-8">
        {data ? (
          <div className="p-8 border border-gray-300 rounded-lg">
            <h1 className="text-3xl font-bold mb-4">{data.title}</h1>
            <div
              className="text-gray-800 mb-4"
              dangerouslySetInnerHTML={{ __html: data.content }}
            />
            {cookies.accessToken && (
                <div className="space-x-2">
                  <button
                    onClick={handleEdit}
                    className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600"
                  >
                    
                  </button>
                  <button
                    onClick={handleDelete}
                    className="bg-red-500 text-white px-4 py-2 rounded hover:bg-red-600"
                  >
                    
                  </button>
                </div>
              )}
          </div>
        ) : (
          <p>  ...</p>
        )}
      </div>
    </div>
  );
}
